.signUpContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 81px);

  .signUpForm {
    width: 430px;
    margin: 0 auto;
    box-shadow: rgba(190, 190, 190,0.4) 0px 0px 7px;
    border-radius:10px;
  
    :global{
      .ant-card-body{
        padding: 24px 32px;
      }
    }
   
  }
  @media screen and (max-width: 450px) {
    min-height: 100vh;
    .signUpForm{
      width: 320px;
      min-width: 320px;
      :global{
        .ant-card-body{
          padding: 12px 16px;
        }
      }
    }
  }

  .formContainer {
    margin-top: 3rem;
    :global{
      .ant-form-item{
        margin-bottom: 24px;
      }
    }
  }
  .formTitle{
    padding-bottom: 16px;
    margin-left: -25px;
    margin-right: -25px;
  
    img{
      width: 80%;
      height: 100%;
      margin-bottom: 20px;
    }
  }
  .customInputSignUp {
    border-radius: 5px;
    padding: 7px 10px;
   
  
  }
  .btnSignUp {
    padding: 22px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin-top: 1rem;
    background: #50c297;
    border: none;
    font-size:16px;
    margin-bottom: 1.5rem;
    &:hover{
      background: #50c297;
    }
    &:focus{
      background: #50c297;
    }
  }
  .wrapperImage{
    background-color: #fff7f7;
    height:calc(100vh - 81px);
    width:50%;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      width: 600px;
      height: 500px;

      @media screen and (max-width:600px) {
        width: 100%;
      }
    }
    @media screen and (max-width:600px) {
      height: 100vh;
    }
  }
  .forgotPassword{
    border-top: 1px solid #b5b5b582;
    padding-top: 24px;
    text-align: center;
  
    a{
      color: #333;
      font-size:14px;
    }
  }
}





